.icon {
  width: 30px;
  height: 30px;
  margin: 10px;
  margin-right: 30px;
}

.tech-header {
  display: flex;
  justify-content: left;
  align-items: center;
}

.tech-list {
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: left;
  margin-right: 20%;
  margin-left: 20%;
  margin-top: 20px;
}

.major-tech-detail {
  padding: 20px;
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.major-tech-detail:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.major-tech-excerpt {
  font-size: 0.9em;
  padding: 30px;
}

.odd {
  background-color: #ffffff;
}

.even {
  background-color: #ffffff;
}

.tech-title {
  display: flex;
  flex-direction: column;
}

.tech-title h3 {
  font-weight: bold;
}

.tech-title h4 {
  font-weight: lighter;
  font-size: 1.2em;
}


.minor-tech-card-icon {
  width: 50px;
  height: 50px;
  margin: 10px;
}

.minor-tech-cards {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  width: 100%;
}

.minor-tech-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 20%;
  min-width: 25px;
  max-width: 200px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  background-color: #ffffff;
}

.minor-tech-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
  transition: 0.3s;
}

.minor-tech-card-content h3 {
  font-weight: bold;
  font-size: 1.1em;
}

.minor-tech-card-content h4 {
  font-weight: lighter;
  font-size: 0.8em;
}

.h2 {
  text-align: center;
}


.new-tech-card-icon {
  width: 50px;
  height: 50px;
  margin: 10px;
}

.new-tech-cards {
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  width: 100%;

  justify-content: center;
}

.new-tech-card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 20%;
  min-width: 25px;
  max-width: 200px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  background-color: #ffffff;
}

.new-tech-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transform: scale(1.01);
  transition: 0.3s;
}

.new-tech-card-outer {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 20%;
  min-width: 25px;
  max-width: 200px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  background-color: #ffffff;
  text-decoration: none;
}

.new-tech-card-outer:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transform: scale(1.01);
  transition: 0.3s;
}

.new-tech-card-content h3 {
  font-size: 0.9em;
}

.new-tech-card-content h4 {
  font-weight: lighter;
  font-size: 0.8em;
}

.new-tech-list {
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-right: 20%;
  margin-left: 20%;
  margin-top: 20px;
}

.new-tech-list h2 {
  font-weight: bold;
}